.container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
}
.copypaste{
    text-align: center;
    color: white;
    font-size: 7rem;
    
}

.listBox{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    /* width: 60vw; */
}
.selectlist{
    margin: 15px;
}
.color{
    height: 100px;
    width: 100px;
    border-radius: 20px 20px;
    border: 3px whitesmoke groove;
}
.hexcolor{
    text-align: center;
}
.rgbcolor{
    text-align: center;
}
@media (max-width:600px){
    .copypaste{
        text-align: center;
        font-size: 5rem;
    }

    .color{
        height: 75px;
        width: 75px;
    }

}